\n            .place__position {\n                background: #BD6844;\n            }\n        }\n\n        &__card {\n            padding: 10px 16px;\n            display: flex;\n            flex-direction: column;\n            align-items: center;\n            justify-content: center;\n            border-radius: 16px;\n            width: 100%;\n            background-color: #1F1B29;\n\n            &-title {\n                white-space: nowrap;\n                text-overflow: ellipsis;\n                overflow: hidden;\n                color: var(--color-text-secondary);\n                font-size: 14px;\n                font-weight: 300;\n                margin-bottom: 7px;\n            }\n\n            &-value {\n                white-space: nowrap;\n                text-overflow: ellipsis;\n                overflow: hidden;\n                max-width: 100px;\n                color: var(--color-text);\n            }\n\n            &-reward {\n                white-space: nowrap;\n                text-overflow: ellipsis;\n                overflow: hidden;\n                color: #47d524;\n                font-weight: 500;\n            }\n        }\n\n        &__img {\n            width: 100px;\n            height: auto;\n            aspect-ratio: 1/1;\n            flex-shrink: 0;\n            border-radius: 8px;\n            overflow: hidden;\n            object-position: center;\n            object-fit: cover;\n            margin-bottom: 16px;\n        }\n    }\n\n    .leaders-table {\n        transition: max-height 0.4s ease, padding-top 0.4s ease;\n        padding-bottom: 12px;\n        max-height: 1200px;\n        background-color: #15101D;\n        font-weight: 400;\n        &::-webkit-scrollbar {\n            width: 0;\n            height: 0;\n        }\n        thead th:first-child,\n        tbody td:first-child,\n        thead th:last-child,\n        tbody td:last-child {\n            width: 20px;\n            padding: 0;\n            border: 0 !important;\n        }\n\n        thead,\n        tr {\n            border: none;\n        }\n\n        td {\n            font-weight: 300;\n        }\n\n        th {\n            font-size: 15px;\n            color: #fff;\n            font-weight: 300;\n        }\n        th,\n        td {\n            white-space: nowrap;\n            padding: 16px 12px;\n            margin: 15px 5px;\n            border-color: #ffffff16;\n            border-bottom-width: 1px;\n        }\n        td.not-current {\n            color: rgba(255, 255, 255, 0.3);\n            white-space: nowrap;\n            padding-left: 12px;\n            padding-right: 12px;\n        }\n        \n        tr:last-child {\n            td {\n                margin-bottom: 20px;\n                border: none;\n            }\n        }\n        &.is_open {\n            max-height: 0 !important;\n             padding: 0;\n        }\n    }\n}\n"],"sourceRoot":""}]);
